  const [trE2, setTRE2] = React.useState(false);
  const classes = useStyles();
  const showNotification = place => {
    switch (place) {
      case "tr":
        if (!tr) {
          setTR(true);
          setTimeout(function () {
            setTR(false);
          }, 6000);
        }
        break;
      case "trC":
      if (!trC) {
        setTRC(true);
        setTimeout(function () {
          setTRC(false);
        }, 6000);
      }
      break;
      case "trP":
      if (!trP) {
        setTRP(true);
        setTimeout(function () {
          setTRP(false);
        }, 6000);
      }
      break;
      case "trA":
      if (!trP) {
        setTRA(true);
        setTimeout(function () {
          setTRA(false);
          
        }, 3000);
      }
      break;
      case "trE1":
      if (!trE1) {
        setTRE1(true);
        setTimeout(function () {
          setTRE1(false);
        }, 6000);
      }
      break;
      case "trE2":
      if (!trE2) {
        setTRE2(true);
        setTimeout(function () {
          setTRE2(false);
        }, 6000);
      }
      break;
      default:
        break;
    }
  };

  const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
  
  const saveCookies = (idUsuario, nombre, correo, edad, idRol, pass) => {
        cookie.save("idUsuario", idUsuario, { path: "/" });
        cookie.save("nombre", nombre, { path: "/" });
        cookie.save("correo", correo, { path: "/" });
        cookie.save("edad", edad, { path: "/" });
        cookie.save("idRol", idRol, { path: "/" });
        cookie.save("pass", pass, { path: "/" });
  }

  const selectFile = async () => {    
    const file = document.querySelector('#file-input').files[0]
    const result = await toBase64(file).catch(e => e);
    if (result instanceof Error) {
      console.log('Error: ', result.message);
      return;
    }
    state.avatar64 = `'${result}'`
    document.getElementById('avatarProfile').src = result
  };


const changeToPass = () => {
  const divPass = document.getElementById('divPass')
  const divContainer = document.getElementById('divContainer')
  divPass.style.display = 'block'
  divContainer.style.display = 'none'
}

